body {
  margin: 0;
  margin-bottom: 50px;
}

body,
input,
textarea,
button {
  font-family: 'Poppins', sans-serif;
}

.body-block {
  opacity: 0;
  height: 0;
  overflow: hidden;
  -webkit-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
  margin: 0;
}

.body-block.is-active {
  margin: auto;
  margin-top: 50px;
  max-width: 780px;
  padding: 0 5%;
}

.body-block.is-active {
  opacity: 1;
  height: auto;
}
